/* USER PROFILE PAGE */

/*BEGIN TAB*/
.card {
  padding: 30px;
  background-color: rgba(214, 224, 226, 0.2);
  -webkit-border-top-left-radius: 5px;
  -moz-border-top-left-radius: 5px;
  border-top-left-radius: 5px;
  -webkit-border-top-right-radius: 5px;
  -moz-border-top-right-radius: 5px;
  border-top-right-radius: 5px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.card.hovercard {
  position: relative;
  padding-top: 0;
  overflow: hidden;
  text-align: center;
  background-color: #fff;
  background-color: rgba(255, 255, 255, 1);
}

/* BEGIN MODEL */
.contain {
  width: 460px;
  height: 590px;
  margin: 0 auto;
  position: relative;
}
.body {
  width: 250px;
  height: 500px;
  background: url("./../images/allbody/bodynew.png");
  position: absolute;
  top: 12%;
  left: 22%;
}
.topclothes {
  width: 250px;
  height: 500px;
  background: url("./../images/allbody/bikini_branew.png");
  position: absolute;
  top: 12%;
  left: 22%;
  z-index: 1;
}

.botclothes {
  width: 250px;
  height: 500px;
  background: url("./../images/allbody/bikini_pantsnew.png");
  position: absolute;
  top: 12%;
  left: 22%;
  z-index: 1;
}

.model {
  width: 60px;
  height: 77px;
  background: url("./../images/model/1000new.png");
  position: absolute;
  top: 5%;
  left: 45.5%;
  z-index: 1;
}

.handbags {
  width: 500px;
  height: 1000px;
  position: absolute;
  bottom: -40.5%;
  right: -3.5%;
  transform: scale(0.5);
  z-index: 4;
}

.necklaces {
  width: 500px;
  height: 1000px;
  position: absolute;
  bottom: -40%;
  right: -3.5%;
  transform: scale(0.5);
  z-index: 4;
}

.hairstyle {
  width: 1000px;
  height: 1000px;
  position: absolute;
  top: -75%;
  right: -57%;
  transform: scale(0.15);
  z-index: 4;
}

.shoes {
  width: 500px;
  height: 1000px;
  background: url("./../images/shoes/shoes1.png");
  position: absolute;
  bottom: -37%;
  right: -3.5%;
  transform: scale(0.5);
  z-index: 1;
}

.background {
  width: 900px;
  height: 1500px;
  background-image: url("./../images/background/background1.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  bottom: -90%;
  right: -50%;
  transform: scale(0.5);
  z-index: -1;
}

/* END MODEL */

hr {
  height: 10px;
  border: 0;
  box-shadow: 0 10px 10px -10px #8c8b8b inset;
}

.nav-pills .nav-item {
  width: 140px;
  text-align: center;
}
.nav-pills .nav-item .nav-link {
  border: 1px solid #ccc;
  border-radius: 0 !important;
}
.well {
  background-color: white;
  height: 700px;
  width: 100%;
}

.card {
  border: none;
  padding-top: 30px;
}

.over {
  z-index: 100;
}
.under {
  z-index: -1;
  top: 0;
  left: 0;
}

.thuDoApp {
  max-width: 1583px;
}
